import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { TranslateService } from '@ngx-translate/core';

import { InitiativeService } from '../../../../core/services/initiative.service';
import { LanguageService } from '../../../../core/services/language.service';
import { SupportFormService } from './support-form.service';

import { Country } from '../../../../core/models/country.model';
import { SupportForm } from '../../../../core/models/supportForm.model';
import { Property } from '../../../../core/models/property.model';
import { HttpErrorResponse } from '@angular/common/http';
import { ApplicationService } from '../../../../core/services/application.service';

@Component({
    selector: 'app-form',
    templateUrl: 'form.component.html'
})

export class FormComponent implements OnInit {
    systemCountries: Country[] = [];
    countries: any[] = []; // residence country selector
    supportForm: SupportForm;
    selectedCountry = '#';
    residentValue = '#';
    alertResident = false;
    optionalValidation = false;
    optionalValidationSelected = false;

    // To check
    showFields: boolean = false;
    submitted: boolean = false;
    valid: boolean = false;
    issuingAuthorityField: boolean = false;
    urlRegister: string;
    public nationalities: any[] = [];
    public optionsBoolean: any;

    constructor(
        private applicationService: ApplicationService,
        private initiativeService: InitiativeService,
        private translateService: TranslateService,
        private languageService: LanguageService,
        private supportFormService: SupportFormService,
        private router: Router
    ) {}

    get hasResident() {
        return this.supportForm && this.supportForm.hasResident;
    }

    get warningResident() {
        return 'form.in-' + this.supportForm.countryCode + '-allowed';
    }

    get hasDocuments() {
        return this.supportForm && this.supportForm.documentType;
    }

    get requirementsCountry(): string {
        const countryCode = this.supportForm.countryCode;

        const text: string = this.translateService.instant('common.requirements.' + countryCode);
        const url = 'http://eur-lex.europa.eu/legal-content/' + this.languageService.selectedLanguage
            + '/TXT/PDF/?uri=OJ:JOL_2015_178_R_0001';
        const link: string = this.translateService.instant('common.requirements.link', { url: url });

        return text.replace('{0}', link);
    }

    ngOnInit() {
        this.initiativeService.getSystemCountries().subscribe(data => {
            this.systemCountries = data;
        });
        this.initiativeService.getCustomisations().subscribe(data => {
            this.optionalValidation = data.optionalValidation;
        });
    }

    onSelectCountry(countryCode) {
        if (countryCode === null || countryCode === '#') {
            this.supportForm = null;
            this.showFields = null;
        } else {
            this.loadCountryProperties(countryCode);
        }

        // this.clearErrors();
    }

    onSelectResident(value) {
        this.residentValue = value;

        if (this.residentValue === '#') {
            this.alertResident = false;
            this.showFields = false;
        } else if (value === '0') {
            this.alertResident = true;
            this.showFields = false;
        } else {
            this.alertResident = false;
            this.showFields = true;
        }
    }

    public onDocumentChange(property: Property) {
        this.supportForm.documentType = property;
    }

    public onPropertyChange(property: Property) {
        this.supportForm.updateProperty(property);
    }

    public onCaptchaChange(property: Property) {
        this.supportForm.captcha = property;
    }

    public onChangePrivacy(event) {
        this.supportForm.privacy.value = event.target.checked;
    }

    public OnChangeCertify(event) {
        this.supportForm.certify.value = event.target.checked;
    }

    loadCountryProperties(countryCode: string) {
        this.initiativeService.getCountryProperties(countryCode)
            .subscribe(data => this.setCountryForm(countryCode, data));
    }

    getResidentOptions() {
        const options = [
            { id: '#', value: 'common.select' },
            { id: '1', value: 'common.yes' },
            { id: '0', value: 'common.no' }
        ];

        return options;
    }

    public setCountryForm(countryCode, properties) {
        this.supportForm = this.supportFormService.loadProperties(countryCode, properties);
        if (this.supportForm.hasResident) {
            this.showFields = false;
        } else {
            this.showFields = true;
        }
    }

    urlPrivacy() {
        return '#/privacy?lang=' + this.languageService.selectedLanguage;
    }

    getUrlRegister() {
        return this.urlRegister;
    }

    getCountryOptions() {
        let first = [{ id: '#', text: this.translateService.instant('common.select-country') }];
        let options = this.countries;

        return first.concat(options);
    }

    getCountriesOptionsForCountry(country: string = null) {
        let options = this.getCountryOptions();

        if (options.length) {
            if (country === 'uk' || country === 'ie') {
                options = options.filter(item => item.id === country || item.id === '#');
            } else if (country && country !== '#') {
                options.push({ id: 'other', text: this.translateService.instant('common.other-country') });
            }
        }

        return options;
    }

    getNationalityOptions() {
        let first = [{ id: '#', text: this.translateService.instant('common.select-nationality') }];
        let options = this.nationalities;

        return first.concat(options);
    }

    public showPersonalFields() {
        return this.showFields && this.supportForm.hasPersonalFields();
    }

    public showAddressFields() {
        return this.showFields && this.supportForm.hasAddressFields();
    }

    public showDocumentFields() {
        return this.showFields && this.supportForm.hasDocumentsFields();
    }

    public showCertifyInfo() {
        return this.showPersonalFields() || this.showAddressFields();
    }

    // check if the current selected country need to show the label permanent residence

   // Show checkbox for ignore some errors and continue
   // If user has ckecked the input, continue showing the checkbox and checked
    public showOptionalValidation() {
        return this.showFields && this.hasErrorsIgnore() && this.optionalValidation;
    }

    public showCaptcha() {
        return this.showFields;
    }

    public isValidated() {
        return this.submitted ? this.valid : true;
    }

    public onSubmit(event) {
        let fields: any[];
        let field;
        let error = false;

        this.submitted = true;
        if (this.validate() === false) {
            this.valid = false;
            this.supportFormService.refreshCaptcha();
            this.scrollToErrors();
            return false;
        }

        this.valid = true;
        this.insertSignature();
    }

    public insertSignature() {
        const fields = this.getPropertiesForApi();

        const language = this.initiativeService.initiative.description.languageCode;

        this.initiativeService.insertSignature(
            'en',
            this.supportForm.countryCode,
            fields,
            this.supportForm.captcha,
            this.optionalValidationSelected
        )
            .subscribe(
                (res: any) => {
                    // status = 200
                    this.initiativeService.initiative.signatureIdentifier = res.signatureIdentifier;
                    this.applicationService.signatureIdentifier = res.signatureIdentifier;
                    this.router.navigateByUrl('/screen/submitted');
                },
                (errorRes: HttpErrorResponse) => {
                    // status != 200
                    try {
                        this.supportFormService.processErrorResponse(errorRes.error);
                    } catch (e) {
                        // err json parse
                    }
                    this.supportFormService.refreshCaptcha();
                    this.valid = false;
                    this.scrollToErrors();
                }
            );

    }

    public hasErrorsIgnore() {
        return this.supportFormService.hasErrorsIgnore;
    }

    private clearErrors() {
        this.valid = true;
        this.submitted = false;
        this.supportForm.clearErrors();
    }

    private validate() {
        return this.supportFormService.validate();
    }

    private scrollToErrors() {
        let el = document.querySelector('form h2');

        try {
            el.scrollIntoView(true);
        } catch (e) {
            window.scrollTo(0, 0);
        }
    }

    private setErrorFromLabel(fieldkey: string, errorMessage: string) {
        this.supportFormService.setErrorFromLabel(fieldkey, errorMessage);
    }

    private getPropertiesForApi() {
        return this.supportFormService.getPropertiesForApi();
    }

    private prioritySorting(a, b) {
        if (a.priority > b.priority) {
            return -1;
        }

        if (a.priority < b.priority) {
            return 1;
        }

        return 0;
    }

}



