import { Component, OnInit } from '@angular/core';
import { InitiativeService } from '../../../../core/services/initiative.service';
import { CountryMap } from '../../../../core/models/countryMap.model';

@Component({
    selector: 'app-carrousel',
    templateUrl: 'carrousel.component.html'
})

export class CarrouselComponent implements OnInit {
    distributionMap: CountryMap[];

    constructor(private iniciativeService: InitiativeService) { }

    ngOnInit() {
        this.iniciativeService.getDistributionMap().subscribe(data => {
            this.distributionMap = data;
        });
    }
}



